.product-card {
  width: 200px;
  border: 1px solid $color-lite-gray;
  border-radius: 5px;
  background: $white-color;
  padding: 32px 16px 16px;

  &:nth-child(4n) {
    margin-right: 0;
  }

  &:nth-last-child(-n + 1) {
    margin-right: 0;
  }

  &__image-wrapper {
    text-align: center;
    padding-bottom: 37px;
  }

  &__image {
    width: 160px;
    height: 134px;
    object-fit: cover;
  }

  &__title {
    font-size: 12px;
    font-weight: 500;
    color: $mainAccentColour;
    line-height: 18px;
    padding-bottom: 4px;
  }

  &__code {
    font-size: 10px;
    line-height: 14px;
    font-weight: 400;
    text-align: left;
    color: $secondaryColor;
    padding-bottom: 16px;
  }

  &__price {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    padding-bottom: 15px;
  }

  &__price-title {
    color: $secondaryColor;
    font-size: 12px;
    font-weight: 400;
    line-height: 18px;
  }

  &__price-value {
    font-size: 16px;
    font-weight: 700;
    line-height: 18px;
    color: $mainAccentColour;
  }

  &__buy {
    display: flex;
    align-items: center;
    height: 40px;
    background-color: $blueAccentColour;
    border-radius: 5px;
  
    margin: 0 auto;
    text-decoration: none;
    text-transform: uppercase;
    font-size: 14px;
    font-weight: 700;
    color: $white-color;
    padding: 10px 53px;
    border: 1px solid transparent;
  }
  
  &__buy:hover {
    border-color: $blueAccentColour;
    color: $blueAccentColour;
    background-color: $white-color;
  }
}

.response {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 24px;

  &__count {
    font-size: 10px;
    font-weight: 400;
    line-height: 13px;
    color: $mainAccentColour;
    margin: 0;
  }
}

.stars {
  display: flex;

  &__star {
    width: 16px;
    height: 16px;
    margin-right: 4px;
    background-image: url(../images/star.svg);
    background-repeat: no-repeat;
    background-position: center;
  }

  @for $i from 1 to 5 + 1 {
    &--#{$i} .stars__star:nth-child(-n + #{$i}) {
      background-image: url(../images/star-active.svg);
    }
  }
}
